<script>
import request from "@/utils/request";

export default {
  name: 'recruit',
  data() {
    return {
      companys: []
    }
  },
  methods: {
    applyfor(id){
      request.get('/user/updateInterview/'+id).then(res => {
        this.$message.success("申请成功")
        location.reload()
        return
      })
    } ,


    view(id){
      this.$router.push({
        path: '/jobDetails',
        query: {
          id
        }
      })
      this.$message.success("查看成功")
    }
  },
  mounted() {
    request.post('/user/allCompany').then(res => {
      console.log(res.data.data);
      this.companys = res.data.data
      let length = this.companys.length;
      for (var i=0;i<length;i++) {
        this.companys[i].wage+="元"
        this.companys[i].recruitingNumbers+="人"
      }
    })
    /*   axios.get(`${config.server}/user/all`).then(
           res=>{
             console.log(res.data.data);
             this.users=res.data.data
           }
       )*/
  }
}
</script>
